[data-icon] {
  height: 1em;
  width: 1em;
  display: inline-block; }

[data-btn], [data-btn]:link, [data-btn]:visited {
  background-color: #e60073;
  color: #fff;
  border-radius: 0 0.35rem;
  display: inline-block;
  margin-bottom: 0.7rem;
  padding: 0.175rem 1.4rem; }

[data-btn]:focus, [data-btn]:hover, [data-btn]:active {
  background-color: #ac0056;
  text-decoration: none; }

[data-field] {
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 0.7rem; }

[data-label] {
  font-family: "freight-sans-pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
  background: #fff;
  border-radius: 0 0.35rem;
  color: #266478;
  font-size: 0.71429em;
  font-weight: bold;
  margin: 0 auto -0.35rem 0.35rem;
  padding: 0 0.35rem;
  transition: color 250ms ease; }

[data-input='default'],
[data-input='textarea'] {
  border: 2px solid #266478;
  border-radius: 0 0.35rem;
  font-size: 0.71429em;
  padding: 0.35rem;
  transition: border-color 250ms ease;
  width: 100%; }
  [data-input='default']:invalid,
  [data-input='textarea']:invalid {
    border-color: #e60073; }
  [data-input='default']:focus,
  [data-input='textarea']:focus {
    border-color: #266478;
    outline: 0; }
    [data-input='default']:focus + [data-label],
    [data-input='textarea']:focus + [data-label] {
      color: #e60073; }

[data-input='textarea'] {
  min-height: 8.4rem; }

[data-actions] {
  align-items: center;
  display: flex;
  flex-direction: row-reverse; }

[data-wrap~='intro'] {
  border-bottom: 1px solid #b3c9d0;
  font-size: 1.04296em;
  grid-column: 2 / span 3;
  margin: 1.4rem 0;
  padding-bottom: 0.7rem;
  position: relative; }

[data-grid='feature'] {
  display: grid;
  grid-auto-flow: dense;
  grid-auto-rows: minmax(9.75rem, 1fr);
  grid-gap: 0.7rem;
  grid-template-columns: repeat(auto-fit, minmax(13rem, 1fr));
  padding: 0.7rem; }

[data-feature] {
  background-color: #e60073;
  background-image: var(--img, initial);
  background-size: cover;
  border: 1px solid #b3c9d0; }

@media (min-width: 34.125em) {
  [data-feature='large'] {
    grid-column: auto / span 2;
    grid-row: auto / span 2; } }
